import React, { FC } from 'react'
import clsx from 'clsx'
import { PlusCircleIcon } from '../../icons/PlusCircle'
import { OrderItem } from '../OrderItem'
import { CartItemBundleProps } from '../CartItemBundle/types'
import '../CartItemBundle/CartItemBundle.scss'
const OrderItemBundle: FC<CartItemBundleProps> = ({
  additionalItems = [],
  className,
  isCheckout = false,
  ...parentItem
}) => {
  return (
    <div className={clsx('CartItemBundle', className)}>
      <OrderItem
        className='CartItemBundle__ParentItem'
        isCheckout={isCheckout}
        {...parentItem}
      />
      {additionalItems?.map((item, itemIdx) => (
        <div
          key={`${parentItem.title}-${item.title}-${itemIdx}`}
          className='CartItemBundle__Item'
        >
          <div className='CartItemBundle__ItemDivider'>
            <PlusCircleIcon color='var(--ui-green)' />
            <hr className='CartItemBundle__DividerLine' />
          </div>
          <OrderItem
            className='CartItemBundle__ItemCard'
            isCheckout={isCheckout}
            isCountable={false}
            {...item}
          />
        </div>
      ))}
    </div>
  )
}

export default OrderItemBundle
